nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #0C4036;
    padding: 15px 80px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.06);
    z-index: 999;
    position: sticky;
    top: 0;
    left: 0;
}

.nav-container {
    display: flex;
}

#navbar {
    display: flex;
    align-items: center;
    justify-content: center;
}

#navbar li {
    list-style: none;
    padding: 0 20px;
    position: relative;
}

#navbar li a {
    text-decoration: none;
    font-size: 1.3rem;
    font-weight: 600;
    color: #fff;
    transition: 0.3s ease-in-out;

}

#navbar li a:hover,
#navbar li a.active {
    color: #89AB76;
}

#navbar li a:hover::after,
#navbar li a.active::after {
    content: "";
    width: 50%;
    height: 3px;
    background: #89AB76;
    position: absolute;
    bottom: -4px;
    left: 20px;
}

#shopping-cart {
    padding: 20px;
}

.fa-shoppingcart {
    color: #fff;
    width: 25px;
}

.cart-quantity {
    color: #fff;
    font-size: 14px;
    font-weight: 600;
    margin-left: 5px;

}

@media screen and (max-width: 769px) {
    #navbar {
        display: flex;
        flex-direction: column;
    }

    li {
        margin-bottom: 1rem;
    }
}