.form-container {
    width: 65%;
    display: flex;
    flex-direction: column;
    margin: 1rem auto;
  }
  
  input {
    width: 65%;
    padding: 23px 15px;
    outline: none;
    margin-bottom: 20px;
    border: 1px solid #e1e1e1;
    border-radius: 10px;
  }
  
.message-body {
    height: 15vh;
  }
  
.submit-btn {
    transition: 0.5s;
    border-radius: 0.25em;
    cursor: pointer;
    margin-top: 10px;
    background-color: #F2A916;;
    border: none;
    padding: 1rem;
    color: #fff;
    width: 40%;
    height: 3em;
    text-transform: uppercase;
    }
    
h1 {
    margin: 1rem;
}
