.card-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    margin: 10px;
}
    
    .product {
        padding: 1rem;
        margin: 1rem;
        background: #efefef;
        border-radius: 20px 0px;
        text-align: center;
    }
    
    img {
    width:  300px;
    height: 300px;
    object-fit: cover;
    }
    
    .product:hover {
        box-shadow: 20px 20px 30px rgba(0, 0, 0, 0.02);
        cursor: pointer;
    }
    

    
    .cart-add-btn,
    .view-product-btn {
            background: #F2A916;
            border: none;
            width: 120px;
            padding: 0.75rem;
            color: #fff;
            font-size: 0.75rem;
            font-weight: 600;
            text-transform: uppercase;
            cursor: pointer;
            border-radius: 0.2rem;
            margin: 10px;
    }
