footer {
    display: flex;
    margin-top: 1rem;
    background-color: #89AB76;
    color: #fff;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 30px 50px 0 50px;
}

.col {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 20px;
}

.logo {
    margin-bottom: 30px;
}

footer h4 {
    font-size: 14px;
    padding-bottom: 20px;
}

footer p {
    font-size: 13px;
    margin: 0 0 8px 0;
    text-decoration: none;
    margin-bottom: 10px;
}

footer a {
    font-size: 15px;
    text-decoration: none;
    color: #fff;
    margin-bottom: 10px;
}

footer .socials i {
    color: #465b52;
    padding-right: 4px;
    cursor: pointer;
}

footer .copyright {
    width: 100%;
    text-align: center;
}